nav {
    background: rgba(52, 52, 52, 0.25);
    width:max-content;
    display:block;
    padding: 0.7rem 1.7rem;
    position:fixed;
    left: 50%;
    transform:translate(-50%);
    bottom:2rem;
    display:flex;
    gap: 1.2rem;
    border-radius:3rem;
    backdrop-filter: blur(15px);
    z-index:2;
}

nav a {
    background: transparent;
    padding: 0.6rem;
    border-radius: 50%;
    display:flex;
    color: var(--color-light);
    font-size:1.2rem;

}

nav a:hover {
    background: rgba (0, 0, 0, 0.1);
}

nav a.active {
    background: var(--color-bg);
    color: white;
}

