@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style:none;
    text-decoration:none;
    max-width:100vw;
    
   
}

:root {
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.79);

    --container-width-lg: 100%;
    --container-width-md: 100%;
    --container-width-ms: 100%;
 

    
}

html {
    scroll-behavior: smooth;
  
}

::-webkit-scrollbar {
    display:none;
}

body {
    font-family: 'Poppins', sans-serif;
    
    color: var(--color-white);
    line-height: 1.7;
    max-width:100vw;
    /*background-color: #120d40;
    background-image: url(../src/assets/texture.svg);*/
    background-color: #1a0736;
background-image: url("data:image/svg+xml,%3Csvg width='12' height='16' viewBox='0 0 12 16' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 .99C4 .445 4.444 0 5 0c.552 0 1 .45 1 .99v4.02C6 5.555 5.556 6 5 6c-.552 0-1-.45-1-.99V.99zm6 8c0-.546.444-.99 1-.99.552 0 1 .45 1 .99v4.02c0 .546-.444.99-1 .99-.552 0-1-.45-1-.99V8.99z' fill='%23380e75' fill-opacity='0.4' fill-rule='evenodd'/%3E%3C/svg%3E");
    
}

/* ====== GENERAL STYLES ====== */

.container {
    margin-left: 8vw;
    margin-right: 8vw;
    justify-content: center;
  
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top:8rem;
    min-height:100vh;
    
}

section > h2, 
section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    background: white;
    color: var(--color-bg);
    padding: 0.55rem 1.5rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid  var(--color-bg);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    object-fit:contain;
}

/* ======= MEDIA QUERIES (MEDIUM DEVICES) ======= */

@media screen and (max-width: 1024px) {
    .container {
       
         margin-left: 5vw;
         margin-right: 5vw;
        justify-content: center;
        
    }
    section {
        margin-top: 6rem;
    }
}

/* ======= MEDIA QUERIES (SMALL DEVICES) ======= */

@media screen and (max-width: 768px) {
    .container {
        width: var(--container-width-SM);
    }
    section > h2 {
        margin-bottom: 6rem;
    }

    img {
        width:100%;
    }
}