footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align:center;
    font-size: 0.9rem;
    margin-top:1rem;
    padding-bottom:2.6rem;
}

footer a {
    color: var(--color-bg);
}

.footer__logo {
    font-size:2rem;
    font-weight:500;
    margin-bottom:2rem;
    display:inline-block;
}

.permalinks {
    display:flex;
    flex-wrap:wrap;
    justify-content: center;
    gap:2rem;
    margin: 0 auto 2rem;
}

.footer__socials {
    display:flex;
    justify-content:center;
    gap:1rem;
    margin-bottom:2rem;
}

.footer__socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius:0.7rem;
    display:flex;
    border:1px solid transparent;
    font-size:1rem;

}

.footer__socials a:hover {
    background:transparent;
    color:var(--color-bg);
    border-color:var(--color-bg);
}

.footer__copyright {
    margin-bottom:4rem;
    color:var(--color-bg);
}



/* ======= MEDIA QUERIES (SMALL DEVICES) ======= */

@media screen and (max-width: 768px) {
   .permalinks{
       flex-direction:column;
       gap:1.5rem;
   }
}


